Vue.prototype.getLogion = function(userData) {
  if (window.plus) {
    var appUser = plus.storage.getItem("appUser");
  } else {
    var appUser = localStorage.getItem('appUser');
  }
  if (appUser != undefined & appUser != null & appUser != "") {
    // userData.callback();
  } else {
    this.$router.push({
      path: '/logion'
    })
  }
}
Vue.prototype.getUser=function(){
	if (window.plus) {
	  var appUser = plus.storage.getItem("appUser");
	} else {
	  var appUser = localStorage.getItem('appUser');
	}
  return JSON.parse(appUser);
}
