<template>
  <div id="Phone">
    <mu-paper :z-depth="1" class="demo-list-wrap">
      <mu-appbar color="lightBlue400">
        <mu-button icon slot="left" @click="goIndex()">
          <mu-icon value="menu"></mu-icon>
        </mu-button>联系人
        <!-- <mu-button icon slot="right">
          <mu-icon value="search"></mu-icon>
        </mu-button>-->
      </mu-appbar>

      <mu-list>
        <mu-sub-header>Today</mu-sub-header>
        <mu-list-item v-for="(item,index) in PhoneData" :key="index" avatar button :ripple="false">
          <mu-list-item-action>
            <mu-avatar>
              <img src="../assets/img/tximg.jpg" />
            </mu-avatar>
          </mu-list-item-action>
          <mu-list-item-content>
            <mu-list-item-title>{{item.name}}</mu-list-item-title>
            <mu-list-item-sub-title>{{item.phone}}</mu-list-item-sub-title>
          </mu-list-item-content>
        </mu-list-item>
      </mu-list>
    </mu-paper>
  </div>
</template>
<script>
export default {
  name: "Phone",
  data() {
    return {
      msg: "电话资源",
      PhoneData: []
    };
  },
  created() {
    this.loadData();
  },
  methods: {
    loadData() {
      let PhoneData = [];
      for (var i = 0; i < 30; i++) {
        let item = {};
        item.name = "程坤";
        item.phone = "13977777777";
        PhoneData.push(item);
      }
      this.PhoneData = PhoneData;
    },
    goIndex() {
      this.$router.push({
        path: "/"
      });
    }
  }
};
</script>
<style scoped lang="scss">
</style>


