<template>
  <div id="Phone">
    <mt-header :title='msg'>
      <router-link to='/' slot='left'>
        <mt-button icon='back'>返回</mt-button>
      </router-link>
    </mt-header>
    <mu-tabs :value.sync="active" color="#ef4f4f" indicator-color="yellow" full-width>
      <mu-tab>自由巡检</mu-tab>
      <mu-tab>上报故障</mu-tab>
    </mu-tabs>
    <div class="demo-text" v-if="active === 0">
      <mu-load-more @refresh="refresh" :refreshing="refreshing" :loading-text="loadTitle" :loaded-all="isOver" :loading="loading"
        @load="load">
        <mu-expansion-panel v-for="(item,index) in dataList" :key="index">
          <div slot="header">{{item.statusContent}}</div>
          <div class="contentBox">
            <span>巡检名称:</span>
            {{item.statusContent}}
          </div>
          <div class="contentBox">
            <span>时间:</span>
            {{item.createTime}}
          </div>
          <mu-button slot="action" flat @click="goMap(item.id)" color="primary">查看</mu-button>
        </mu-expansion-panel>
      </mu-load-more>
    </div>
    <div class="demo-text" v-if="active === 1">
      <mu-load-more @refresh="refresh2" :refreshing="refreshing2" :loading-text="loadTitle2" :loaded-all="isOver2"
        :loading="loading2" @load="load2">
        <mu-expansion-panel v-for="(item,index) in dataList2" :key="index">
          <div slot="header">{{item.content}}</div>
          <div class="contentBox">
            <img style="display: block;width: 150px;height: 300px;margin: auto;" :src="SrcApi+item.fileImg" alt="">
          </div>
          <div class="contentBox">
            <span>故障内容:</span>
            {{item.content}}
          </div>
          <div class="contentBox">
            <span>上报时间:</span>
            {{item.createTime}}
          </div>
          <!-- <mu-button slot="action" flat @click="goPosition(item)" color="primary">查看</mu-button> -->
        </mu-expansion-panel>
      </mu-load-more>
    </div>
  </div>
</template>
<script>
  import axios from "axios";
  import qs from "qs";
  export default {
    name: "Phone",
    data() {
      return {
        msg: "历史记录",
        active: 0,
        dataList: [],
        refreshing: false,
        loading: false,
        isOver: false,
        loadTitle: "正在加载。。",
        dataList2: [],
        refreshing2: false,
        loading2: false,
        isOver2: false,
        loadTitle2: "正在加载。。",
        userId: "",
        page: 1,
        page2: 1
      };
    },
    created() {
      this.userId = this.getUser().creatUserId;
      this.getData();
      this.getData2();
    },
    methods: {
      goIndex() {
        this.$router.push({
          path: "/"
        });
      },
      //自由巡检
      //刷新
      refresh() {
        this.isOver = false;
        this.refreshing = true;
        this.page = 1;
        this.getData();
      },
      //加载更多
      load() {
        this.loading = true;
        this.page=this.page + 1;
        this.getData('load');
      },
      //获取数据
      getData(type) {
        let _this = this;
        axios
          .post(
            _this.SrcApi + "/line/findLineOfMe",
            qs.stringify({
              userId: this.userId,
              pageNum: this.page
            })
          )
          .then(function(response) {
            _this.refreshing = false;
            _this.loading = false;
            if (response.data.state == 1000) {
              if (response.data.data.endRow== response.data.data.total) {
                _this.isOver = true;
              }
              if (type == 'load') {
                _this.dataList = _this.dataList.concat(response.data.data.list);
                console.log('新增成功');
              } else {
                _this.dataList = response.data.data.list;
                console.log('获取故障点位成功');
              }
            } else {
              _this.endPatrol();
              console.log("获取故障点位失败");
              return;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      //故障点位
      //刷新
      refresh2() {
        this.isOver2 = false;
        this.refreshing2 = true;
        this.page2 = 1;
        this.getData2();
      },
      //加载更多
      load2() {
        this.loading2 = true;
        this.page2=this.page2 + 1;
        this.getData2('load');
      },
      //获取数据
      getData2(type) {
        let _this = this;
        axios
          .post(
            _this.SrcApi + "/trouble/findLineOfMe",
            qs.stringify({
              userId: this.userId,
              pageNum: this.page2
            })
          )
          .then(function(response) {
            _this.refreshing2 = false;
            _this.loading2 = false;
            if (response.data.state == 1000) {
              if (response.data.data.endRow== response.data.data.total) {
                _this.isOver2 = true
              }
              if (type == 'load') {
                _this.dataList2 = _this.dataList2.concat(response.data.data.list);
                console.log('新增成功');
              } else {
                _this.dataList2 = response.data.data.list;
                console.log('获取故障点位成功');
              }
            } else {
              _this.endPatrol();
              console.log("获取故障点位失败");
              return;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      goMap(lineId) {
        if (lineId != null) {
          this.$router.push({
            name: "HistoryMap",
            params: {
              lineId
            }
          });
        }
      },
      goPosition(coor) {
        if (coor != null && coor.length != 0) {
          this.$router.push({
            name: "PositionMap",
            params: {
              coor
            }
          });
        }
      }
    }
  };
</script>
<style scoped lang="scss">
  #Phone {
    width: 100%;
    height: 100%;
    position: relative;

    .demo-text {
      position: absolute;
      bottom: 0;
      right: 0;
      left: 0;
      top: 88px;
      overflow-y: auto;
    }
  }
</style>



