<template>
  <div class="MapBox">
    <mt-header title="查看线路">
      <router-link to="/history" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <!-- <mt-button icon="more" slot="right" @click=""></mt-button> -->
    </mt-header>
    <div id="map"></div>
  </div>
</template>
<script>
  // import * as L from "leaflet";
  import axios from "axios";
  import qs from "qs";
  export default {
    name: "GGMap",
    data() {
      return {
        map: null,
        lineId: null,
        latlngs: []
      };
    },
    created() {
      this.mapCoorIni();
      //页面刚进入时开启长连接
    },
    destroyed: function() {
      //页面销毁时关闭长连接
    },
    mounted() {
      this.polylin();
    },
    methods: {
      mapCoorIni() {
        let _this = this;
        if (this.$route.params.lineId != null) {
          _this.lineId = this.$route.params.lineId;
          console.log("进入地图")
        } else {
          _this.$router.push({
            path: "/history"
          });
        }
      },
      //初始化标记划线
      polylin() {
        let _this = this;
        axios
          .post(
            this.SrcApi + "/line/gpsOfLine",
            qs.stringify({
              lineId: _this.lineId
            }), {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
              }
            }
          )
          .then(function(response) {
            debugger;
            if (response.data.state == 1000) {
              if (response.data.data != null && response.data.data.length != 0) {
                let data = response.data.data;
                for (let i = 0; i < data.length; i++) {
                  let arr = [data[i].latitude, data[i].longitude]
                  _this.latlngs.push(arr)
                }
                var normalm = L.tileLayer.chinaProvider('GaoDe.Normal.Map', {
                  maxZoom: 18,
                  minZoom: 5
                });
                var imgm = L.tileLayer.chinaProvider('GaoDe.Satellite.Map', {
                  maxZoom: 18,
                  minZoom: 5
                });
                var imga = L.tileLayer.chinaProvider('GaoDe.Satellite.Annotion', {
                  maxZoom: 18,
                  minZoom: 5
                });
                var normal = L.layerGroup([normalm]),
                  image = L.layerGroup([imgm, imga]);

                var baseLayers = {
                  "地图": normal,
                  "影像": image,
                }

                _this.map = L.map("map", {
                  center: _this.latlngs[0],
                  zoom: 15,
                  layers: [normal],
                  zoomControl: true
                });

                for (var i = 0; i < _this.latlngs.length; i++) {
                  var item = _this.latlngs[i];
                  L.marker(item).addTo(_this.map);
                }
                var latlngs = _this.latlngs;
                var polyline = L.polyline(latlngs, {
                  color: "red"
                }).addTo(_this.map);
              } else {
                _this.$router.push({
                  path: "/history"
                });
                return;
              }
            } else {
              _this.$router.push({
                path: "/history"
              });
              return;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    }
  };
</script>
<style scoped lang="scss">
  .MapBox {
    width: 100%;
    height: 100%;
    position: relative;

    #map {
      position: absolute;
      top: 40px;
      left: 0;
      right: 0;
      bottom: 0;
      // width: 100%;
      // height: 100%;
    }
  }
</style>



