<template>
  <div class="modify">
    <div class="logo">
      <!-- <mu-row justify-content="center"> -->
      <!-- <mu-avatar size="100"> -->
      <img src="../assets/img/logo.png" />
      <!-- </mu-avatar> -->
      <!-- </mu-row> -->
    </div>
    <div class="box">
      <mt-field label="旧密码" :state="pwdStaOld" v-model="oldPassword" placeholder="请输入密码" type="password"></mt-field>
      <div class="Dboorder"></div>
      <mt-field label="密码" :state="pwdSta" v-model="password" placeholder="请输入密码" type="password"></mt-field>
      <div class="Dboorder"></div>
      <mt-button class="loginBtn" type="danger" @click="modify" size="large">确定</mt-button>
     <router-link to='/' class="returnBtn">取消修改</router-link>
    </div>
  </div>
</template>

<script>
  import {
    Toast
  } from "mint-ui";
  import axios from "axios";
  import qs from "qs";
  export default {
    name: 'HomeHead',
    props: {},
    data() {
      return {
        msg: '修改密码',
        oldPassword: "",
        password: "",
        pwdSta: "",
        pwdStaOld: "",
        username: ""
      }
    },
    methods: {
      handleClose: function() {},
      modify() {
        let _this=this;
        if (this.oldPassword != "") {
          if (this.password != "") {
            if (this.oldPassword == this.getUser().password) {
              axios
                .post(
                  this.SrcApi + "/user/upDatePassword",
                  qs.stringify({
                    'oldPassword': _this.oldPassword,
                    'username': _this.username,
                    'password': _this.password
                  }), {
                    headers: {
                      "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
                    }
                  }
                )
                .then(function(response) {
                  if (response.data.state == 1000) {
                    Toast({
                      message: response.data.message,
                      iconClass: "icon icon-success"
                    });
                    _this.$router.push({
                      path: '/logion'
                    })
                  }
                })
                .catch(function(error) {
                  console.log(error);
                  Toast({
                    message: error,
                    iconClass: "icon icon-success"
                  });
                });
            } else {
              this.pwdStaOld = "error";
            }
          } else {
            this.pwdSta = "error";
          }
        } else {
          this.pwdStaOld = "error";
        }
      }
    },
    created() {
      this.username = this.getUser().name;
    }

  }
</script>

<style scoped lang="scss">
  .modify {
    width: 100%;
    height: 100%;

    .box {
      width: 80%;
      margin: auto;

      .loginBtn {
        margin-top: 30px;
      }

      .Dboorder {
        width: 100%;
        height: 0px;
        border-top: 1px solid #d9d9d9;
      }
      .returnBtn{
        display: block;
        float: right;
        color: #000000;
        padding: 8px 15px;
      }
    }

    .logo {
      width: 215px;
      height: 215px;
      margin: auto;
      // margin-top: 55px;
      margin-bottom: 172px;

      img {
        width: 100%;
        height: 100%;
        margin-top: 105px;
      }
    }
  }
</style>



