<template>
  <div class='FaultMap'>
    <mt-header :title='msg'>
      <router-link to='/Fault' slot='left'>
        <mt-button icon='back'>返回</mt-button>
      </router-link>
    </mt-header>
    <div id="faultMap" style=""></div>
  </div>
</template>

<script>
  export default {
    name: 'FaultMap',
    props: {},
    data() {
      return {
        msg: '故障定点',
        map: null,
      }
    },
    mounted() {
      //   this.getCenter();
      // this.MapIni();
      this.inIMap();
    },
    methods: {
      inIMap() {
        let _this = this;
        let latlngs = [this.$route.params.longitude, this.$route.params.latitude]


        // _this.map = L.map("map", {
        //   center: _this.latlngs[0],
        //   zoom: 15
        // });
        // L.tileLayer(
        //   "http://webrd0{s}.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=8&x={x}&y={y}&z={z}", {
        //     subdomains: ["1", "2", "3", "4"],
        //     attribution: "高德"
        //   }
        // ).addTo(_this.map);

        _this.map = L.map("faultMap", {
          center: latlngs,
          zoom: 15
        });
        L.tileLayer(
          "http://webrd0{s}.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=8&x={x}&y={y}&z={z}", {
            subdomains: ["1", "2", "3", "4"],
            attribution: "高德"
          }
        ).addTo(_this.map);
        var mypop = L.popup();
        var marker = L.marker(latlngs).addTo(_this.map);
      }
    },
    created() {
      if(this.$route.params.longitude==null||this.$route.params.longitude==undefined){
        this.$router.push({
          path: "/Fault"
        });
      }
    }

  }
</script>

<style scoped lang="scss">
  .FaultMap {
    width: 100%;
    height: 100%;
    position: relative;

    #faultMap {
      position: absolute;
      bottom: 0;
      left: 0;
      top: 40px;
      right: 0;
      // width:100%;
      // height:300px
    }
  }
</style>



