<template>
  <div class='myUser'>
    <mu-drawer :open.sync="open" :docked="docked" width="100%" right="right">
      <mt-field label="用户名" placeholder="请输入用户名" v-model="name"></mt-field>
      <mt-field label="密码" placeholder="请输入密码" v-model="password"></mt-field>
      <mt-field label="电话" placeholder="请输入号码" v-model="phone"></mt-field>
      <div class="window-btn" style="text-align: center;">
        <mt-button type="primary" size="normal" @click="add()">添加</mt-button>
      </div>
      <div class="window-btn">

        <mt-button type="primary" size="normal" @click="open=!open">取消</mt-button>
      </div>
    </mu-drawer>

    <mu-drawer :open.sync="openSelect" :docked="docked" width="100%" right="right">
      <mt-field label="用户名" placeholder="请输入用户名" v-model="selectName"></mt-field>
      <div class="window-btn" style="text-align: center;">
        <mt-button type="primary" size="normal" @click="selectCont()">查询</mt-button>
      </div>
      <div class="window-btn">

        <mt-button type="primary" size="normal" @click="openSelect=!openSelect">取消</mt-button>
      </div>
    </mu-drawer>

    <mt-header :title='msg'>
      <router-link to='/' slot='left'>
        <mt-button icon='back'>返回</mt-button>
      </router-link>
      <!-- <mt-button slot='right' @click="showAdd()">添加</mt-button> -->
      <!-- &nbsp; -->
      <!-- &nbsp; -->
      <!-- <mt-button slot='right' @click="openSelect=!openSelect">查询</mt-button> -->
    </mt-header>

    <div style="height: 44px;width: 100%;display: flex;">
      <div style="width: 100%;">
        <mt-search cancel-text="取消" placeholder="搜索" v-model="selectName"></mt-search>
      </div>
      <div @click="selectCont" style="width: 55px;height: 44px;text-align: center;line-height: 44px;background: #d9d9d9;color: #26a2ff;">确定</div>
    </div>
    <div class="content">
      <mu-load-more @refresh="refresh" :refreshing="refreshing" loading-text="正在加载.." :loaded-all="isOver" :loading="loading" @load="load">
      <mu-list>
        <mu-list-item v-for="(item,index) in userList" :key="index" avatar button :ripple="false">
          <mu-list-item-action>
            <mu-avatar>
              <img src="../assets/img/tximg.jpg" />
            </mu-avatar>
          </mu-list-item-action>
          <mu-list-item-content @click="goPhone(item.phone)">
            <mu-list-item-title>{{item.name}}</mu-list-item-title>
            <mu-list-item-sub-title>{{item.phone}}</mu-list-item-sub-title>
          </mu-list-item-content>

          <mu-list-item-action v-if='isAdmin==true'>
            <mu-button icon @click="deleteUser(item)">
              <mu-icon value="info"></mu-icon>
            </mu-button>
          </mu-list-item-action>
        </mu-list-item>
      </mu-list>
      </mu-load-more>
    </div>

  </div>
</template>

<script>
  import axios from "axios";
  import qs from "qs";
  import {
    Dialog
  } from 'vant'
  export default {
    name: 'myUser',
    props: {},
    data() {
      return {
        msg: '用户',
        userList: null,
        open: false,
        docked: false,
        name: '',
        password: '',
        userId: '',
        phone: '',
        isAdmin: false,
        openSelect: false,
        selectName: '',
        //分页加载
        refreshing:false,
        loading:false,
        isOver:false,
        page:1
      }
    },
    methods: {
      handleClose: function() {},
      goPhone(val) {
        window.location.href = 'tel://' + val
      },
      add() {
        let _this = this;
        axios
          .post(
            _this.SrcApi + "/user/addUser",
            qs.stringify({
              name: _this.name,
              password: _this.password,
              userId: _this.userId,
              phone: _this.phone
            }), {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
                // Authorization: authorParams
              }
            }
          )
          .then(function(response) {
            if (response.data.state == 1000) {
              _this.showAdd();
              _this.getUserList();
            } else {
              // _this.endPatrol();
              console.log("获取用户信息失败");
              return;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      selectCont() {
        let _this = this;
        axios
          .post(
            _this.SrcApi + "/user/findByNameLike",
            qs.stringify({
              name: _this.selectName,
            }), {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
                // Authorization: authorParams
              }
            }
          )
          .then(function(response) {
            if (response.data.state == 1000) {
              _this.userList = response.data.data;
              _this.selectName = "";
              console.log('获取用户信息成功');
            } else {
              // _this.endPatrol();
              console.log("获取用户信息失败");
              return;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      showAdd() {
        this.open = !this.open;
      },
      deleteUser(val) {
        let _this = this;
        Dialog.confirm({
          title: '提醒信息',
          message: '是否确定删除该用户？'
        }).then(() => {
          axios
            .post(
              this.SrcApi + "/user/delUser",
              qs.stringify({
                id: val.id
              }), {
                headers: {
                  "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
                  // Authorization: authorParams
                }
              }
            )
            .then(function(response) {
              if (response.data.state == 1000) {
                console.log("删除成功");
                _this.getUserList();
              } else {
                return;
              }
            })
            .catch(function(error) {
              console.log(error);
            });
        }).catch(() => {
          // on cancel
        });
      },
      getUserList(type) {
        let _this = this;
        axios
          .post(
            _this.SrcApi + "/user/list",
            qs.stringify({
              pageNum: this.page
            }))
          .then(function(response) {
            _this.refreshing = false;
            _this.loading = false;
            if (response.data.state == 1000) {
              if(response.data.data.endRow==response.data.data.total){
                  _this.isOver=true;
              }
              if(type=='load'){
                _this.userList = _this.userList.concat(response.data.data.list);
                console.log('新增成功');
              }else{
                _this.userList = response.data.data.list;
                console.log('获取故障点位成功');
              }
            } else {
              _this.endPatrol();
              console.log("获取故障点位失败");
              return;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      refresh() {
        this.isOver=false;
        this.refreshing = true;
        this.page=1;
        this.getUserList();
      },
      //加载更多
      load() {
        this.loading = true;
        this.page=this.page+1;
        this.getUserList('load');
      },
    },
    created() {
      this.userId = this.getUser().creatUserId;
      if (this.getUser().id == '001') {
        this.isAdmin = true;
      }
      this.getUserList();

    }

  }
</script>

<style scoped lang="scss">
  .mint-header .mint-button {
    margin-right: 5px;
  }

  .myUser {
    position: absolute;
    width: 100%;
    height: 100%;

    .content {
      position: absolute;
      top: 84px;
      left: 0;
      right: 0;
      bottom: 0;
      overflow-y: auto;
    }
  }
</style>



