<template>
  <div class='addResour'>

    <mu-drawer :open.sync="open" :docked="docked" width="100%" right="right">
      <mu-form :model="form" class="mu-demo-form" :label-position="labelPosition" label-width="100">
        <mu-form-item prop="name" label="线路名称">
          <mu-text-field v-model="form.name"></mu-text-field>
        </mu-form-item>
        <mu-form-item prop="type" label="线路类型">
          <mu-select v-model="form.type">
            <mu-option v-for="(item,index) in typeList" :key="index" :label="item" :value="item"></mu-option>
          </mu-select>
        </mu-form-item>
        <mu-form-item>
          <mu-button color="primary" @click="add()">提交</mu-button>
          <mu-button @click="showAdd()">取消</mu-button>
        </mu-form-item>
      </mu-form>

      <!-- <mt-button type="primary" size="large" @click="add()">添加</mt-button> -->
    </mu-drawer>

    <mt-header :title='msg'>
      <router-link to='/resources' slot='left'>
        <mt-button icon='back'>返回</mt-button>
      </router-link>
      <!-- <mt-button icon='more' slot='right' @click="showAdd()"></mt-button> -->
    </mt-header>
    <div id="map"></div>
    <mu-ripple class="footer demo-2 left" color="yellow" @click="showAdd()" :opacity="0.5">添加</mu-ripple>
    <!-- <mu-ripple class="footer demo-2 left" color="yellow" @click="add()" :opacity="0.5">重置</mu-ripple> -->
    <mu-ripple class="footer demo-2 right" color="yellow" @click="deletePosition()" :opacity="0.5">删除</mu-ripple>
  </div>
  </div>
</template>

<script>
  import axios from "axios";
  import qs from "qs";
  export default {
    name: 'addResour',
    props: {},
    data() {
      return {
        msg: '系统线路添加',
        userId: '',
        positionList: [],
        map: null,
        myPolyline: null,
        myMarker: null,
        open: false,
        docked: false,
        form: {
          name: '',
          type: ''
        },
        typeList: ['管道', '架空','直埋','附挂','寄挂'],
        labelPosition: 'top'
      }
    },
    methods: {
      showAdd() {
        // this.$refs.form.clear();
        this.open = !this.open
      },
      add() {

        let _this = this;
        let list=""
        for(let i=0;i<_this.positionList.length;i++){
          let item=_this.positionList[i];
          list+=item[0]+"-"+item[1]+","
        }
        axios
          .post(
            _this.SrcApi + "/systemLine/addLine",
            qs.stringify({
              userId: _this.userId,
              statusContent:_this.form.type,
              name:_this.form.name,
              //list:JSON.stringify(_this.positionList).toString()
              // list:_this.positionList
              list:list
            }), {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
                // Authorization: authorParams
              }
            }
          )
          .then(function(response) {
            if (response.data.state == 1000) {
              _this.showAdd();
            } else {
              console.log("获取用户信息失败");
              return;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      deletePosition() {
        let _this = this;
        if (_this.myPolyline != null) {
          _this.map.removeLayer(_this.myPolyline);
        }
        if (_this.myMarker != null) {
          _this.myMarker.clearLayers();
        }
        _this.positionList.pop();
        let myMarkers = [];
        for (let i = 0; i < _this.positionList.length; i++) {
          let item = _this.positionList[i];
          let marker = new L.Marker(item);
          myMarkers.push(marker)
        }
        _this.myMarker = L.layerGroup(myMarkers);
        _this.map.addLayer(_this.myMarker);
        _this.myPolyline = L.polyline(_this.positionList, {
          color: "red"
        }).addTo(_this.map);
      },
      addMarker(position) {
        let _this = this;
        let positonItem=[position.lat,position.lng];
        _this.positionList.push(positonItem);
        if (_this.myPolyline != null) {
          _this.map.removeLayer(_this.myPolyline);
        }
        if (_this.myMarker != null) {
          _this.myMarker.clearLayers();
        }
        let myMarkers = [];
        for (let i = 0; i < _this.positionList.length; i++) {
          let item = _this.positionList[i];
          let marker = new L.Marker(item);
          myMarkers.push(marker)
        }
        _this.myMarker = L.layerGroup(myMarkers);
        _this.map.addLayer(_this.myMarker);
        _this.myPolyline = L.polyline(_this.positionList, {
          color: "red"
        }).addTo(_this.map);
      },
      mapIni() {
        var _this = this;
        _this.map = L.map("map", {
          center: [29.37, 113.09],
          zoom: 15
        });
        L.tileLayer(
          "http://webrd0{s}.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=8&x={x}&y={y}&z={z}", {
            subdomains: ["1", "2", "3", "4"],
            attribution: "高德"
          }
        ).addTo(_this.map);
        var mypop = L.popup();
        _this.map.on("click", function(e) {
          _this.addMarker(e.latlng);
        });
      }
    },
    mounted() {
      this.mapIni()
    },
    created() {
      this.userId = this.getUser().creatUserId;
    }

  }
</script>

<style scoped lang="scss">
  .addResour {
    width: 100%;
    height: 100%;

    #map {
      width: 100%;
      position: absolute;
      top: 40px;
      left: 0;
      bottom: 1.2rem;
      right: 0;
      overflow: hidden;
    }

    .footer {
      width: 50%;
      height: 1.2rem;
      position: absolute;
      // left: 0;
      bottom: 0;
      overflow: hidden;
      text-align: center;
      line-height: 1.2rem;
      background: #00796b;
      color: #fff;
      font-size: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
      // margin-right: 16px;
    }

    .left {
      left: 0;
    }

    .right {
      right: 0;
    }

    &.demo-2 {
      background-color: #2196f3;
      color: #fff;
    }
  }
</style>



