<template>
  <div class="MapBox">
    <div id="map"></div>
  </div>
</template>
<script>
// import * as L from "leaflet";
export default {
  name: "positionMap",
  data() {
    return {
      msg: "历史巡检故障",
      latlngs: null
    };
  },
  created() {
    this.getPosition();
  },
  mounted() {
      this.iniMap();
  },
  methods: {
    getPosition() {
      let _this = this;
      let positionCoor = this.$route.params.coor;
      if (positionCoor != undefined) {
        if (positionCoor != null && positionCoor.Start.length != 0) {
          _this.latlngs = positionCoor;
        } else {
          _this.$router.push({
            path: "/history"
          });
        }
      } else {
        _this.$router.push({
          path: "/history"
        });
      }
    },
    iniMap() {
      let _this = this;
      _this.map = L.map("map", {
        center: _this.latlngs.Start,
        zoom: 13
      });
      L.tileLayer(
        "http://webrd0{s}.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=8&x={x}&y={y}&z={z}",
        {
          subdomains: ["1", "2", "3", "4"],
          attribution: "高德"
        }
      ).addTo(_this.map);
      var mypop = L.popup();
      var item = _this.latlngs.Start;
      var marker = L.marker(item).addTo(_this.map);
      marker.on("click", function(e) {
        console.log(e);
        var content =
          `<div>
                          <p>名称：` +
          _this.latlngs.name +
          ` </p>
                          <p>坐标：` +
          _this.latlngs.Start +
          `</p>
                          <p>原因：` +
          _this.latlngs.type +
          `</p>
                          <p>维修人员：` +
          _this.latlngs.type +
          `</p>
                        </div>`;
        mypop
          .setLatLng(e.latlng)
          .setContent(content)
          .openOn(_this.map);
      });
    }
  }
};
</script>
<style scoped lang="scss">
.MapBox {
  width: 100%;
  height: 100%;
  #map {
    width: 100%;
    height: 100%;
  }
}
</style>


