<template>
  <div class="logoin">
    <div class="logo">
      <!-- <mu-row justify-content="center"> -->
      <!-- <mu-avatar size="100"> -->
      <img src="../assets/img/logo.png" />
      <!-- </mu-avatar> -->
      <!-- </mu-row> -->
    </div>
    <div class="box">
      <mt-field label="用户名" :state="nameSta" v-model="userName" placeholder="请输入用户名"></mt-field>
      <mt-field label="密码" :state="pwdSta" v-model="userPwd" placeholder="请输入密码" type="password"></mt-field>
      <div class="Dboorder"></div>
      <mt-button class="loginBtn" type="danger" @click="Logion" size="large">登录</mt-button>
    </div>
  </div>
</template>
<script>
  import {
    Toast
  } from "mint-ui";
  import axios from "axios";
  import qs from "qs";
  export default {
    name: "logoin",
    data() {
      return {
        msg: "logoin",
        userName: "",
        userPwd: "",
        nameSta: "",
        pwdSta: "",
        LoaginData: {
          userName: 'chengkun',
          userPwd: 130130
        }
      };
    },
    methods: {
      Logion() {
        var _this = this;
        axios
          .post(
            this.SrcApi + "/user/login",
            qs.stringify({
              'name': _this.userName,
              'password': _this.userPwd
            }), {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
              }
            }
          )
          .then(function(response) {
            if (response.data.state == 1000) {
              let appUser = JSON.stringify(response.data.data);
              if (window.plus) {
                plus.storage.setItem("appUser", appUser);
              } else {
                localStorage.setItem("appUser", appUser);
              }
              _this.$router.push({
                path: "/"
              });
            }else{
              Toast({
                message: response.data.message,
                iconClass: "icon icon-success"
              });
            }
          })
          .catch(function(error) {
            console.log(error);
            Toast({
              message: error,
              iconClass: "icon icon-success"
            });
          });
        // if (_this.userName == _this.LoaginData.userName) {
        //   _this.nameSta = "success";
        //   if (_this.userPwd == _this.LoaginData.userPwd) {
        //     if (window.plus) {
        //       plus.storage.setItem("appUser","chengkun");
        //       plus.storage.setItem("appKey","130130");
        //     } else {
        //       localStorage.setItem("appUser","chengkun");
        //       localStorage.setItem("appKey","130130");
        //     }

        //     _this.pwdSta = "success";
        //     _this.$router.push({
        //       path: "/"
        //     });
        //   } else {
        //     _this.pwdSta = "error";
        //   }
        // } else {
        //   _this.nameSta = "error";
        // }
      }
    }
  };
</script>
<style scoped lang="scss">
  .logoin {
    width: 100%;
    height: 100%;

    .box {
      width: 80%;
      margin: auto;

      .loginBtn {
        margin-top: 30px;
      }

      .Dboorder {
        width: 100%;
        height: 0px;
        border-top: 1px solid #d9d9d9;
      }
    }

    .logo {
      width: 215px;
      height: 215px;
      margin: auto;
      // margin-top: 55px;
      margin-bottom: 172px;

      img {
        width: 100%;
        height: 100%;
        margin-top: 105px;
      }
    }
  }
</style>



