import Vue from 'vue'
import Router from 'vue-router'
import MapCont from '@/Map/index'
import HistoryMap from '@/Map/historyMap'
import Logion from '@/Logion/logion'
import Index from '@/View/Index'
import Fault from '@/View/Fault'
import Free from '@/View/Free'
import History from '@/View/History'
import List from '@/View/List'
import Phone from '@/View/Phone'
import Resources from '@/View/Resources'
import Task from '@/View/Task'
import LocationMap from '@/Map/LocationMap'
import PositionMap from '@/Map/positionMap'
import FaultMap from '@/Map/FaultMap'
import MyUser from "@/View/MyUser"
import addResour from "@/Map/addResour"
import SeeTask from '@/Map/seeTask'
import Modify from '@/Logion/modify'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/logion',
      name: 'Logion',
      component: Logion
    },
    {
      path: '/historyMap',
      name: 'HistoryMap',
      component: HistoryMap
    },
    {
      path: '/map',
      name: 'MapCont',
      component: MapCont
    },
    {
      path: "/fault",
      name: "Fault",
      component: Fault
    },
    {
      path: "/free",
      name: "Free",
      component: Free
    },
    {
      path: "/history",
      name: "History",
      component: History
    },
    {
      path: "/list",
      name: "List",
      component: List
    },
    {
      path: "/phone",
      name: "Phone",
      component: Phone
    },
    {
      path: "/resources",
      name: "Resources",
      component: Resources
    },
    {
      path: "/locationMap",
      name: "LocationMap",
      component: LocationMap
    },
    {
      path: "/positionMap",
      name: "PositionMap",
      component: PositionMap
    },
    {
      path: "/task",
      name: "Task",
      component: Task
    },
    {
      path: "/faultMap",
      name: "faultMap",
      component: FaultMap
    },
    {
      path: "/myUser",
      name: "myUser",
      component: MyUser
    },
    {
      path:"/addResour",
      name:'addResour',
      component: addResour
    },
    {
      path:'/SeeTask',
      name:'SeeTask',
      component:SeeTask
    },
    {
      path:'/modify',
      name:'Modify',
      component:Modify
    }
  ]
})



