<template>
  <div id="Fault">
    <mu-drawer :open.sync="open" :docked="docked" width="100%" right="right">
      <!-- <mt-field label="类型" placeholder="请输入用户名" v-model="type"></mt-field>
      <mt-field label="类型类容" placeholder="请输入类型类容" v-model="typeContent"></mt-field>
      <mt-field label="任务外键" placeholder="请输入任务外键" v-model="lineId"></mt-field>
      <mt-button type="primary" size="large" @click="add()">添加</mt-button> -->
      <mt-cell-swipe v-for="(item,index) in resourList" :key="index" :title="item.name" :right="[{
          content: '查看',
          style: { background: 'blue', color: '#fff', },
          handler: () => getXLDWList(item)
        }]"></mt-cell-swipe>
    </mu-drawer>
    <mt-header :title='msg'>
      <router-link to='/' slot='left'>
        <mt-button icon='back'>返回</mt-button>
      </router-link>
      <mt-button icon='more' slot='right' @click="showAdd()"></mt-button>
    </mt-header>

    <mt-cell-swipe v-for="(item,index) in dataList" :key="index" :title="item.statusContent" :right="[{
          content: '故障',
          style: { background: 'red', color: '#fff', },
          handler: () => deleteBtn(item)
        },{
          content: '查看',
          style: { background: 'blue', color: '#fff', },
          handler: () => goMap(item)
        }]"></mt-cell-swipe>
  </div>
</template>
<script>
  import axios from "axios";
  import qs from "qs";
  import {
    Dialog
  } from 'vant'
  export default {
    name: "Fault",
    data() {
      return {
        msg: "故障定点",
        dataList: '',
        open: false,
        docked: false,
        lineId: '',
        typeContent: '',
        type: '',
        longitude: '', //经度
        latitude: '', //维度
        resourList:[],
        xldwList:[]
      };
    },
    methods: {
      getXLDWList(val){
        let _this=this;
        axios
          .post(
            this.SrcApi + "/systemLine/gpsOfLine",
            qs.stringify({
              lineId: val.id
            }), {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
                // Authorization: authorParams
              }
            }
          )
          .then(function(response) {
            if (response.data.state == 1000) {

              _this.dataList = response.data.data;
              _this.showAdd();
            } else {
              return;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      getData() {
        let _this = this;
        axios
          .post(
            this.SrcApi + "/systemLine/list",
            qs.stringify({
              pageNum: _this.pageSize
            }), {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
                // Authorization: authorParams
              }
            }
          )
          .then(function(response) {
            if (response.data.state == 1000) {

              _this.resourList = response.data.data.list
            } else {
              return;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      getDataList() {
        let _this = this;
        axios
          .post(
            this.SrcApi + "/systemGps/list",
            qs.stringify({
              pageNum: 1000
            }), {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
                // Authorization: authorParams
              }
            }
          )
          .then(function(response) {
            if (response.data.state == 1000) {
              console.log("查询成功");
              _this.dataList = response.data.data.list;
              console.log(_this.dataList)
            } else {
              return;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      showAdd() {
        this.open = !this.open;
      },
      add() {
        let _this = this;
        if (navigator.geolocation) {
          console.log("/* 地理位置服务可用 */");
          navigator.geolocation.getCurrentPosition(
            function(position) {
              let userId = _this.getUser().creatUserId;
              _this.latitude = position.coords.latitude; //纬度
              _this.longitude = position.coords.longitude; //经度
              axios
                .post(
                  _this.SrcApi + "/systemGps/addGps",
                  qs.stringify({
                    'type': _this.type,
                    'typeContent': _this.typeContent,
                    'lineId': _this.lineId,
                    'longitude': _this.latitude,
                    'latitude': _this.latitude,
                    "userId": userId
                  }), {
                    headers: {
                      "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
                      // Authorization: authorParams
                    }
                  }
                )
                .then(function(response) {
                  console.log(response.data.state)
                  if (response.data.state == 1000) {
                    _this.showAdd();
                    _this.getDataList();
                    Notify({
                      type: 'success',
                      message: '添加成功'
                    });
                  } else {
                    return;
                  }
                })
                .catch(function(error) {
                  console.log(error);
                });
            },
            function() {
              console.log("定位失败，请打开手机定位");
            }
          );
        } else {
          console.log("/* 地理位置服务不可用 */");
          // "id":"e7614c0ac8f749b6bd947a597941a983",
          // "longitude":29.476567,
          // "latitude":29.475567,
          // "statusContent":"线路点位",
          // "lineId":"89fc8185cb804896bd64c88e46951564",
          // "createTime":"2019-09-28T03:04:10.000+0000",
          // "creatUserId":"123456"
        }
      },
      deleteBtn(val) {
        let _this = this;
        Dialog.confirm({
          title: '标题',
          message: '是否确定表位故障点位？'
        }).then(() => {
          let userId = _this.getUser().creatUserId;
          axios
            .post(
              _this.SrcApi + "/systemGps/addGps",
              qs.stringify({
                'lineId':val.lineId,
                "userId": userId,
                'statusContent':'故障点位',
                'latitude':val.latitude,
                'longitude':val.longitude
              }), {
                headers: {
                  "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
                  // Authorization: authorParams
                }
              }
            )
            .then(function(response) {
              console.log(response.data.state)
              if (response.data.state == 1000) {
                // _this.showAdd();
                // _this.getDataList();
                // Notify({
                //   type: 'success',
                //   message: '添加成功'
                // });
              } else {
                return;
              }
            })
            .catch(function(error) {
              console.log(error);
            });
        }).catch(() => {
          // on cancel
        });
      },
      goMap(val) {
        this.$router.push({
          name: 'faultMap',
          params: val
        })
      }
    },
    created() {
      this.getDataList();
      this.getData();
    }
  };
</script>
<style scoped lang="scss">

</style>



