<template>
  <div class='header'>

    <mu-drawer :open.sync="open" :docked="docked" width="100%" :right="position === 'right'">

      <mt-field label="类型" placeholder="请输入用户名" v-model="type"></mt-field>
      <mt-field label="类型类容" placeholder="请输入类型类容" v-model="typeContent"></mt-field>
      <mt-field label="任务外键" placeholder="请输入任务外键" v-model="toDoId"></mt-field>
      <div class="window-btn" style="text-align: center;">
        <mt-button type="primary" size="normal" @click="selectCont()">查询</mt-button>
      </div>
      <div class="window-btn">

        <mt-button type="primary" size="normal" @click="open=!open">取消</mt-button>
      </div>
    </mu-drawer>
    <mt-header :title='msg'>
      <router-link to='/' slot='left'>
        <mt-button icon='back'>返回</mt-button>
      </router-link>
      <!-- <mt-button slot='right' @click="showAdd()">查询</mt-button> -->
    </mt-header>
    <div class="content">
      <mu-tabs :value.sync="active" color="#ef4f4f" indicator-color="yellow" full-width>
        <mu-tab>未完成</mu-tab>
        <mu-tab>已完成</mu-tab>
      </mu-tabs>
      <div class="demo-text" v-if="active === 0">
        <mu-load-more @refresh="refresh" :refreshing="refreshing" loading-text="正在加载.." :loaded-all="isOver" :loading="loading" @load="load">
          <mu-expansion-panel v-for="(item,index) in dataListFalse" :key="index">
            <div slot="header">{{item.statusContent}}</div>
            <div class="contentBox">
              <span>任务人:</span>
              {{item.userId}}
            </div>
            <div class="contentBox">
              <span>类型内容:</span>
              {{item.statusContent}}
            </div>
            <div class="contentBox">
              <span>开始时间:</span>
              {{item.beginTime}}
            </div>
            <div class="contentBox">
              <span>结束时间:</span>
              {{item.endTime}}
            </div>
            <mu-button slot="action" flat v-if="item.myLineId!=null" @click="see(item)" color="primary">查看</mu-button>
            <!-- <mu-button slot="action" flat @click="deleteItem(item,'0')" color="primary">删除</mu-button> -->
            <!-- <mu-button slot="action" flat  color="primary">查看</mu-button> -->
          </mu-expansion-panel>
        </mu-load-more>
      </div>
      <div class="demo-text" v-if="active === 1">
        <mu-load-more @refresh="refresh2" :refreshing="refreshing2" loading-text="正在加载.." :loaded-all="isOver2" :loading="loading2" @load="load2">
          <mu-expansion-panel v-for="(item,index) in dataList" :key="index">
            <div slot="header">{{item.statusContent}}</div>
            <div class="contentBox">
              <span>任务人:</span>
              {{item.userId}}
            </div>
            <div class="contentBox">
              <span>类型内容:</span>
              {{item.statusContent}}
            </div>
            <div class="contentBox">
              <span>开始时间:</span>
              {{item.beginTime}}
            </div>
            <div class="contentBox">
              <span>结束时间:</span>
              {{item.endTime}}
            </div>
            <mu-button slot="action" flat v-if="item.myLineId!=null" @click="see(item)" color="primary">查看</mu-button>
            <!-- <mu-button slot="action" flat @click="deleteItem(item,'1')" color="primary">删除</mu-button> -->
          </mu-expansion-panel>
        </mu-load-more>
      </div>
    </div>
  </div>

</template>
<script>
  import axios from "axios";
  import qs from "qs";
  import {
    Dialog
  } from 'vant'
  export default {
    name: "Task",
    data() {
      return {
        msg: "巡检任务",
        'type': '',
        'typeContent': '',
        'toDoId': '',
        'beginTime': '',
        'endTime': '',
        'userId': '',
        docked: false,
        open: false,
        position: 'right',
        pickerValue: '',
        //已完成
        dataList: [],
        refreshing2: false,
        loading2: false,
        page2: 1,
        isOver: false,
        //未完成
        dataListFalse:[],
        refreshing: false,
        loading: false,
        page: 1,
        isOver2: false,

        active: 0
      };
    },
    methods: {
      selectCont() {},
      see(val) {
        this.$router.push({
          name: "SeeTask",
          params: {
            id: val.toDoId,
            type: val.statusContent,
            myLineId:val.myLineId
          }
        });
      },
      deleteItem(val,type) {
        let _this = this;
        Dialog.confirm({
          title: '标题',
          message: '是否确定删除该任务？'
        }).then(() => {
          axios
            .post(
              this.SrcApi + "/tasks/delTasks",
              qs.stringify({
                id: val.id
              }))
            .then(function(response) {
              if (response.data.state == 1000) {
                console.log("删除成功");
                if(type==0){
                  _this.getDataListFalse();
                }else if(type==1){
                  _this.getDataListTrue();
                }
                // _this.getDataList();
              } else {
                return;
              }
            })
            .catch(function(error) {
              console.log(error);
            });
        }).catch(() => {
          // on cancel
        });


      },
      getDataList() {
        let _this = this;
        axios
          .post(
            this.SrcApi + "/tasks/list",
            qs.stringify({
              pageNum: 1
            }))
          .then(function(response) {
            if (response.data.state == 1000) {
              console.log("查询成功");
              _this.dataList = response.data.data.list;
              console.log(_this.dataList)
            } else {
              return;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      getDataListTrue(type){
        let _this = this;
        axios
          .post(
            this.SrcApi + "/tasks/findOfMe",
            qs.stringify({
              userId: _this.userId,
              finish:true,
            }))
          .then(function(response) {

            _this.refreshing2 = false;
            _this.loading2 = false;
            if (response.data.state == 1000) {
              if (response.data.data.endRow == response.data.data.total) {
                _this.isOver2 = true;
              }
              if (type == 'load') {
                _this.dataList = _this.dataList.concat(response.data.data);
                console.log('新增成功');
              } else {
                _this.dataList = response.data.data;
                console.log('获取故障点位成功');
              }
            } else {
              _this.endPatrol();
              console.log("获取故障点位失败");
              return;
            }
            // if (response.data.state == 1000) {
            //   console.log("查询成功");
            //   _this.dataList = response.data.data;
            //   console.log(_this.dataList)
            // } else {
            //   return;
            // }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      refresh2() {
        this.isOver2=false;
        this.refreshing2 = true;
        this.page2=1;
        this.getDataListTrue();
      },
      //加载更多
      load2() {
        this.loading2 = true;
        this.page2=this.page2+1;
        this.getDataListTrue('load');
      },

      getDataListFalse(type){
        let _this = this;
        axios
          .post(
            this.SrcApi + "/tasks/findOfMe",
            qs.stringify({
              userId: _this.userId,
              finish:false
            }))
          .then(function(response) {
            _this.refreshing = false;
            _this.loading = false;
            if (response.data.state == 1000) {
              if (response.data.data.endRow == response.data.data.total) {
                _this.isOver = true;
              }
              if (type == 'load') {
                _this.dataListFalse = _this.dataListFalse.concat(response.data.data);
                console.log('新增成功');
              } else {
                _this.dataListFalse = response.data.data;
                console.log('获取故障点位成功');
              }
            } else {
              _this.endPatrol();
              console.log("获取故障点位失败");
              return;
            }

            // if (response.data.state == 1000) {
            //   console.log("查询成功");
            //   _this.dataListFalse = response.data.data;
            //   console.log(_this.dataList)
            // } else {
            //   return;
            // }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      refresh() {
        this.isOver=false;
        this.refreshing = true;
        this.page=1;
        this.getDataListFalse();
      },
      //加载更多
      load() {
        this.loading = true;
        this.page=this.page+1;
        this.getDataListFalse('load');
      },



      showAdd() {
        this.open = !this.open;
      },
      add() {
        let _this = this;
        if (_this.beginTime != null && _this.beginTime != undefined) {
          let beginTime = this.dataFormat(_this.beginTime.getTime(), true)
        } else {
          return;
        }

        if (_this.endTime != null && _this.endTime != undefined) {
          let endTime = this.dataFormat(_this.endTime.getTime(), true)
        } else {
          return;
        }
        axios
          .post(
            this.SrcApi + "/tasks/addTasks",
            qs.stringify({
              'type': _this.type,
              'typeContent': _this.typeContent,
              'toDoId': _this.toDoId,
              'beginTime': beginTime,
              'endTime': endTime,
              "userId": _this.userId
            }))
          .then(function(response) {
            if (response.data.state == 1000) {
              _this.showAdd();
              _this.getDataList();
              Notify({
                type: 'success',
                message: '添加成功'
              });
            } else {
              return;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      dataFormat(num, type) {
        var dd = new Date(num);
        var y = dd.getFullYear();
        var m = dd.getMonth() + 1;
        var d = dd.getDate();
        var h = dd.getHours();
        var i = dd.getMinutes();
        var s = dd.getSeconds();
        m = m < 10 ? '0' + m : m;
        d = d < 10 ? '0' + d : d;
        h = h < 10 ? '0' + h : h;
        i = i < 10 ? '0' + i : i;
        s = s < 10 ? '0' + s : s;
        if (type) {
          return y + '-' + m + '-' + d + ' ' + h + ':' + i + ':' + s
        } else {
          return y + '-' + m + '-' + d
        }
      }
    },
    created() {
      this.userId = this.getUser().id;
      this.getDataListTrue();
      this.getDataListFalse();
    }
  };
</script>
<style scoped lang="scss">
  .window-btn {
    width: 50%;
    float: left;
    text-align: center;
    margin-top: 35px;
  }
  .demo-text {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: 88px;
    overflow-y: auto;
  }
</style>



