<template>
  <div id="list">
    <!-- <mu-container ref="container" class="demo-loadmore-content"> -->
    <mu-load-more @refresh="refresh" :refreshing="refreshing" :loading="loading" @load="load">
      <div class="list-box" v-for="(item,index) in num" :key="index">
        <mu-card>
          <mu-card-header title="header Title" sub-title="header Title">
            <mu-avatar slot="avatar">
              <img src="../assets/img/tximg.jpg" />
            </mu-avatar>
          </mu-card-header>
          <mu-card-media title="img Title" sub-title="img Title">
            <img src="../assets/img/tximg.jpg" />
          </mu-card-media>
          <mu-card-title title="Content Title" sub-title="Content Title"></mu-card-title>
          <mu-card-text>
            散落在指尖的阳光，我试着轻轻抓住光影的踪迹，它却在眉宇间投下一片淡淡的阴影。
            调皮的阳光掀动了四月的心帘，温暖如约的歌声渐起。
            似乎在诉说着，我也可以在漆黑的角落里，找到阴影背后的阳光，
            找到阳光与阴影奏出和谐的旋律。我要用一颗敏感赤诚的心迎接每一缕滑过指尖的阳光！
          </mu-card-text>
          <mu-card-actions>
            <mu-button flat>Action 1</mu-button>
            <mu-button flat>Action 2</mu-button>
          </mu-card-actions>
        </mu-card>
      </div>
    </mu-load-more>
    <!-- </mu-container> -->
  </div>
</template>
<script>
var data = [];
for (var i = 0; i < 30; i++) {
  let item = {};
  item.name = "程坤";
  item.age = "23";
  item.bz = "此处有错误";
  data.push(item);
}
export default {
  name: "list",
  data() {
    return {
      msg: "list",
      num: data,
      refreshing: false,
      loading: false,
      text: "List"
    };
  },
  methods: {
    refresh() {
      this.refreshing = true;
      this.$refs.container.scrollTop = 0;
      setTimeout(() => {
        this.refreshing = false;
        this.text = this.text === "List" ? "Menu" : "List";
        this.num = 10;
      }, 2000);
    },
    load() {
      this.loading = true;
      setTimeout(() => {
        this.loading = false;
        this.num += 10;
      }, 2000);
    }
  }
};
</script>
<style scoped lang="scss">
#index{
    .list-box{
        width:100%;
        margin:auto;
        margin-bottom:3px;
        border-bottom: 1px solid #d9d9d9;
    }
}
</style>


