<template>
  <div id="index">
    <mu-flex class="flex-wrapper" justify-content="center">
      <mu-flex class="flex-demo-left" justify-content="center">
        <!--  -->
        <mu-menu cover>
          <mu-button icon>
            <i></i>
          </mu-button>
          <mu-list slot="content">
            <mu-list-item button @click="modify">
              <mu-list-item-title>修改密码</mu-list-item-title>
            </mu-list-item>
            <mu-list-item button @click="signOut">
              <mu-list-item-title >退出</mu-list-item-title>
            </mu-list-item>
          </mu-list>
        </mu-menu>
      </mu-flex>
      <mu-flex class="flex-demo-center" justify-content="center">
        <mu-flex class="flex-demo-name" justify-content="center">{{userNmae}}</mu-flex>
        <mu-flex class="flex-demo-time" justify-content="center">{{dataTime}}</mu-flex>
      </mu-flex>
      <mu-flex class="flex-demo-right" justify-content="center">
      </mu-flex>
    </mu-flex>
    <div class="index-cont">
      <mu-flex class="cot-wrapper" justify-content="center">
        <mu-flex class="cot-wrapper-nav lx" @click="goDetails('Resources')" justify-content="center">线路资源</mu-flex>
        <mu-flex class="cot-wrapper-nav rw" @click="goDetails('Task')" justify-content="center">巡检任务</mu-flex>
      </mu-flex>
      <mu-flex class="cot-wrapper" justify-content="center">
        <mu-flex class="cot-wrapper-nav dw" @click="goDetails('Fault')" justify-content="center">故障定点</mu-flex>
        <mu-flex class="cot-wrapper-nav xj" @click="goDetails('Free')" justify-content="center">自由巡检</mu-flex>
      </mu-flex>
      <mu-flex class="cot-wrapper" justify-content="center">
        <!-- <mu-flex class="cot-wrapper-nav lxr" @click="goDetails('History')" justify-content="center">历史记录</mu-flex> -->
        <mu-flex class="cot-wrapper-nav lsjl" @click="goDetails('History')" justify-content="center">历史记录</mu-flex>
        <mu-flex class="cot-wrapper-nav lxr" @click="goDetails('myUser')" justify-content="center">用户</mu-flex>
        
      </mu-flex>
<!--      <mu-flex class="cot-wrapper" justify-content="center">

        <mu-flex class="cot-wrapper-nav" justify-content="center"></mu-flex>
      </mu-flex> -->
    </div>
    <div class="bottom">岳阳联通线路巡检</div>
  </div>
</template>
<script>
  export default {
    name: "index",
    data() {
      return {
        msg: "index",
        userNmae: "",
        dataTime: ""
      };
    },
    methods: {
      goDetails(val) {
        let url = val;
        if (url != "" && url != null) {
          this.$router.push({
            name: url,
          });
        }
      },
      signOut(){
        if (window.plus) {
          var appUser = plus.storage.removeItem("appUser");
        } else {
          var appUser = localStorage.removeItem('appUser');
        }
        this.$router.push({
          path: '/logion'
        })
        // localStorage.removeItem('userinfo');
      },
      modify(){
        this.$router.push({
          path: '/modify'
        })
      },
      getTime() {
        var now = new Date();
        var year = now.getFullYear(); //得到年份
        var month = now.getMonth(); //得到月份
        var date = now.getDate(); //得到日期
        var day = now.getDay(); //得到周几
        var hour = now.getHours(); //得到小时
        var minu = now.getMinutes(); //得到分钟
        var sec = now.getSeconds(); //得到秒
        month = month + 1;
        if (month < 10) month = "0" + month;
        if (date < 10) date = "0" + date;
        if (hour < 10) hour = "0" + hour;
        if (minu < 10) minu = "0" + minu;
        if (sec < 10) sec = "0" + sec;
        var time = "";
        time = year + "-" + month + "-" + date;
        return time;
      }
    },
    created() {
      this.getLogion();
      this.userNmae = this.getUser().name;
      this.dataTime = this.getTime();
    }
  };
</script>
<style scoped lang="scss">
  #index {
    width: 100%;
    height: 100%;
    position: relative;

    .flex-wrapper {
      width: 100%;
      height: 1.3rem;
      background: #ef4f4f;
      padding: 0.25rem 0;

      .flex-demo-left {
        width: 15%;
        height: 100%;

        i {
          display: inline-block;
          width: 25px;
          height: 25px;
          background: url(../assets/img/list.png) no-repeat right center;
          background-size: 100% 100%;
        }
      }

      .flex-demo-center {
        width: 65%;
        height: 100%;
        color: #fff;
        font-size: 16px;
      }

      .flex-demo-right {
        width: 20%;
        height: 100%;

        i {
          display: inline-block;
          width: 25px;
          height: 25px;
        }

        .cz {
          background: url(../assets/img/cz.png) no-repeat right center;
          background-size: 100% 100%;
        }

        .gd {
          background: url(../assets/img/gd.png) no-repeat right center;
          background-size: 100% 100%;
        }
      }
    }

    .index-cont {
      width: 100%;

      .cot-wrapper-nav {
        width: 50%;
        text-align: center;
        padding-top: 1.55rem;
      }

      .lx {
        background: url(../assets/img/lx.png) no-repeat center center;
        background-size: 0.55rem 0.55rem;
      }

      .xj {
        background: url(../assets/img/xj.png) no-repeat center center;
        background-size: 0.55rem 0.55rem;
      }

      .dw {
        background: url(../assets/img/dwIcon.png) no-repeat center center;
        background-size: 0.55rem 0.55rem;
      }

      .rw {
        background: url(../assets/img/rw.png) no-repeat center center;
        background-size: 0.55rem 0.55rem;
      }

      .ls {
        background: url(../assets/img/ls.png) no-repeat center center;
        background-size: 0.55rem 0.55rem;
      }

      .lxr {
        background: url(../assets/img/lxr.png) no-repeat center center;
        background-size: 0.55rem 0.55rem;
      }
      
      .lsjl{
        background: url(../assets/img/lsjl.png) no-repeat center center;
        background-size: 0.55rem 0.55rem;
      }
    }

    .bottom {
      width: 100%;
      position: absolute;
      bottom: 0;
      left: 0;
      height: 1.8rem;
      line-height: 1.8rem;
      text-align: center;
      color: #fff;
      background: #ef4f4f;
      font-weight: 700;
      font-size: 20px;
    }
  }
</style>



