<template>
  <div id="Free">
    <mu-drawer :open.sync="showAdd" :docked="docked" width="100%" :right="position === 'right'">
      <div style="padding: 15px;">
        <mu-form :model="form" class="mu-demo-form" enctype="multipart/form-data" :label-position="labelPosition"
          label-width="100">
          <mu-form-item prop="Content" label="故障内容">
            <mu-text-field v-model="form.Content" multi-line :rows="4" full-width name="Content"></mu-text-field>
          </mu-form-item>
          <div style="width: 100%;height:300px;margin-bottom: 25px;">
            <ul>
              <li v-for="(img,index) in items">
                <img v-preview="img.img_url" :src="img.img_url" :alt="img.title" preview-title-enable="true"
                  preview-nav-enable="true" style="width: 150px;height:300px;margin: auto;display: block;">
                <!-- <p>{{img.title}}</p> -->
              </li>
            </ul>
          </div>
          <mu-button style="margin-bottom: 25px;" full-width color="primary" @click="setImg">拍照</mu-button>
          <mu-button style="margin-bottom: 25px;" full-width color="primary" @click="windowMapIni()">选择位置</mu-button>
          <mu-form-item>
            <div style="display: flex;justify-content: center;width: 100%;">
              <mu-button color="primary" @click="saveFault">确定</mu-button>
              <mu-button @click="showAdd=!showAdd">取消</mu-button>
            </div>
          </mu-form-item>
        </mu-form>
      </div>
      <!-- <img :src="img" alt=""> -->
    </mu-drawer>

    <mu-drawer :open.sync="showMap" :docked="docked" width="100%" :right="position === 'right'">
      <div class="windowMapBox">
        <div id="windowMap" class="windowMap"></div>
        <div class="windowMap-foot" style="text-align: center;">
          <div class="windowMap-btn" @click="confirm(1)">确定</div>
          <div class="windowMap-btn" @click="confirm(2)">取消</div>
        </div>
      </div>
    </mu-drawer>

    <mt-header :title='msg'>
      <router-link to='/' slot='left'>
        <mt-button icon='back'>返回</mt-button>
      </router-link>
      <mt-button slot="right" @click="showAdd=!showAdd">故障上报</mt-button>
    </mt-header>
    <div id="MapFree"></div>
    <mu-ripple v-if="ifFree" class="footer demo-2" color="yellow" @click="endPatrol" :opacity="0.5">结束巡检</mu-ripple>
    <mu-ripple v-else class="footer demo-2" color="yellow" @click="startPatrol" :opacity="0.5">开始巡检</mu-ripple>
  </div>
</template>
<script>
  // import * as L from "leaflet";
  // import Toast from "muse-ui-toast";
  import img from "../assets/img/sl.jpg";
  import vuePicturePreview from 'vue-picture-preview';
  import axios from "axios";
  import qs from "qs";
  import $ from 'jquery';
  import {
    Toast
  } from 'mint-ui';
  import {
    MessageBox
  } from 'mint-ui';
  //定义一些常量
  var x_PI = 3.14159265358979324 * 3000.0 / 180.0;
  var PI = 3.1415926535897932384626;
  var a = 6378245.0;
  var ee = 0.00669342162296594323;
  export default {
    name: "Free",
    data() {
      return {
        img_w: ((((window.screen.width) / 33.333) * 0.94) * 0.5) + 'rem',
        msg: "自由巡检",
        end: [113.5, 29.48],
        Start: [],
        map: null, //地图     用于后续添加点位
        timer: null, //定时器  用于后续清除定时器
        myPostion: null, //记录上一个点位  用于判断是否移动
        lines: [], //点位集合
        myPolyline: null, //线路   用于每次添加点位之后清除之前的线路
        myMarker: null, //标识点位   用于后续清除点位
        lineId: null,
        userId: null,
        statusContent: "自由巡检",
        ifFree: false,
        showAdd: false,
        docked: false,
        position: 'right',
        labelPosition: 'top',
        param: new FormData(),
        form: {
          Content: ""
        },
        open: false,
        minImg: null,
        userId: "",
        img: img,
        items: [{
          title: "图片查看",
          img_url: require("../assets/img/sl.jpg"),
        }],
        //弹出层map
        showMap: false,
        windowMap: null,
        selectPosition: null,
        myMarker: null,
        endNum: 0
      };
    },
    components: {
      Previewer: vuePicturePreview
    },
    destroyed() {
      // console.log("")
      if (this.ifFree) {
        console.log("清除定时器")
        this.endPatrol();
      } else {
        console.log("定时器未开启")
      }
    },
    created() {
      this.userId = this.getUser().id;
    },
    mounted() {
      this.inIMap();
    },
    methods: {
      //上传本地图片
      update(e) {
        let that = this;
        let imgFile = $(this.$el).find('#imgLocal')[0].files[0]; //取到上传的图片
        console.log($(this.$el).find('#imgLocal')[0].files); //由打印的可以看到，图片    信息就在files[0]里面
        self.img = $(this.$el).find('#imgLocal')[0].files[0];
        let param = new FormData(); //通过formdata上传
        param.append('imgFile', imgFile);
      },
      saveFault() {
        let _this = this;
        let param = new FormData(); //创建form对象
        console.log(_this.minImg);
        param.append("imgFile", _this.minImg);
        // console.log(_this.userId);
        param.append("userId", _this.userId);
        if (_this.form.Content != null && _this.form.Content != "") {
          param.append("content", _this.form.Content);
        } else {
          return;
        }
        if (_this.selectPosition != null && _this.selectPosition.length != 0) {
          param.append("longitude", _this.selectPosition[1]);
          param.append("latitude", _this.selectPosition[0]);
        } else {
          return;
        }
        console.log(param);
        console.log(param.get('imgFile'));
        axios
          .post(this.SrcApi + "/trouble/addTrouble", param)
          .then(function(response) {
            if (response.data.state == 1000) {
              _this.showAdd = !_this.showAdd
              MessageBox.alert("上报成功", "操作");
            } else {
              _this.showAdd = !_this.showAdd
              MessageBox.alert("上传失败请重新上传", "操作");
            }
            console.log(response.data.state)
            console.log(response.data.message)
            //console.log(response);
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      saveImg() {
        let self = this
        console.log('图片地址' + self.$store.state.user.icon)
        plus.gallery.save(self.$store.state.user.icon, function() {
          self.$toast.success('保存图片到相册成功')
          self.open = false
        }, function() {
          self.$toast.error('保存失败')
        })
      },
      getBase64Image(url) {
        let _this = this;
        var img = new Image();
        img.src = url;
        img.onload = function() {
          var that = img;
          var w = that.width,
            h = that.height,
            scale = w / h;
          w = 320 || w;
          h = w / scale;
          var canvas = document.createElement('canvas');
          var ctx = canvas.getContext('2d');
          $(canvas).attr({
            width: w,
            height: h
          });
          ctx.drawImage(that, 0, 0, w, h);
          var base64 = canvas.toDataURL('image/png', 'image/jpeg', 'image/jpg', 1 || 0.8);
          console.log(base64);
          _this.minImg = base64;
        }
      },
      //拍照
      setImg() {
        let self = this
        let cmr = plus.camera.getCamera()
        cmr.captureImage(function(p) {
          plus.io.resolveLocalFileSystemURL(p, function(entry) {

            self.minImg = self.getBase64Image(entry.toLocalURL());
            self.items[0].img_url = entry.toLocalURL();
          }, function(e) {})
        }, function(e) {}, {
          filename: '_doc/camera/'
        })
      },
      resolveImg(path) {
        this.open = false
        //图片太大,需要压缩
        let self = this
        let substr = path.substr(path.lastIndexOf("/") + 1)
        plus.io.resolveLocalFileSystemURL(path, function(entry) {
          entry.file(function(file) {
            let imgSize = file.size;
            plus.zip.compressImage({
              src: path,
              dst: '_doc/' + substr,
              width: '100%',
              quality: 100,
              overwrite: true
            }, function(event) {
              console.log("压缩成功-->" + event.target)
              let image = new Image();
              image.src = event.target;
              image.style.width = "60px";
              image.style.height = "60px";
              self.minImg = self.getBase64Image(image);
            })
          })
        }, function(e) {
          alert("Resolve file URL failed: " + e.message)
        })
      },
      endPatrol() {
        let _this = this;
        if (_this.endNum <= 1) {
          Toast({
            message: "巡检时间未到，不能结束！",
            position: 'bottom',
            duration: 1000
          });
          return;
        }
        axios
          .post(
            _this.SrcApi + "/line/upDateFinish",
            qs.stringify({
              id: _this.lineId
            })
          )
          .then(function(response) {
            if (response.data.state == 1000) {
              _this.ifFree = false;
              window.clearTimeout(_this.timer);
              Toast({
                message: '已结束自由巡检',
                position: 'bottom',
                duration: 1000
              });
            } else {
              Toast({
                message: '自由巡检结束失败，请重新结束！',
                position: 'bottom',
                duration: 1000
              });
              return;
            }
          })
          .catch(function(error) {
            console.log(error)
            Toast({
              message: '自由巡检结束失败，请重新结束！',
              position: 'bottom',
              duration: 1000
            });
          });
      },
      inIMap() {
        let _this = this;
        if (navigator.geolocation) {
          console.log("/* 地理位置服务可用 */");
          navigator.geolocation.getCurrentPosition(function(position) {
              let latitude = position.coords.latitude; //纬度
              let longitude = position.coords.longitude; //经度
              // let latlon = position.coords.latitude+','+position.coords.longitude;
              latitude = latitude - 0.0026496046771;
              longitude = longitude - (-0.0058125257492);
              // let p = _this.wgs84togcj02(latitude, longitude);
              // console.log(p);
              _this.map = L.map("MapFree", {
                center: [latitude, longitude],
                zoom: 15,
              });
              L.tileLayer(
                "http://webrd0{s}.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=8&x={x}&y={y}&z={z}", {
                  subdomains: ["1", "2", "3", "4"],
                  attribution: "高德"
                }
              ).addTo(_this.map);
              let myMarkers = [];
              let item = [latitude, longitude];
              // let item = p;
              let marker = new L.Marker(item);
              myMarkers.push(marker);
              _this.myMarker = L.layerGroup(myMarkers);
              _this.map.addLayer(_this.myMarker);
              //L.marker([latitude, longitude]).addTo(_this.map);
            },
            function() {
              console.log("/* 地理位置请求失败 */");
              MessageBox.alert('地理位置请求失败,请打开定位设置!').then(action => {
                _this.$router.push({
                  path: "/"
                });
              });
            }, {
              enableHighAccuracy: true, //位置是否精确获取
              timeout: 5000, //获取位置允许的最长时间
              maximumAge: 1000 //多久更新获取一次位置
            });
        } else {
          console.log("/* 地理位置服务不可用 */");
        }
      },
      //开始自由巡检  标记开始位置
      AddMarker(latitude, longitude) {
        let _this = this;
        axios
          .post(
            _this.SrcApi + "/gps/addGps",
            qs.stringify({
              lineId: _this.lineId,
              userId: _this.userId,
              statusContent: _this.statusContent,
              longitude: longitude,
              latitude: latitude
            }), {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
                // Authorization: authorParams
              }
            }
          )
          .then(function(response) {
            if (response.data.state == 1000) {
              console.log("添加点位成功");
              console.log("第一次进入");
              _this.myMarker.clearLayers();
              console.log('清除点位标志')
              _this.myPostion = [latitude, longitude];
              let myMarkers = [];
              let item = [latitude, longitude];
              let marker = new L.Marker(item);
              myMarkers.push(marker);
              _this.lines.push(item);
              _this.myMarker = L.layerGroup(myMarkers);
              _this.map.addLayer(_this.myMarker);
              // L.marker([latitude, longitude]).addTo(_this.map);
            } else {
              Toast({
                message: "添加点位失败",
                position: 'bottom',
                duration: 1000
              });
              // _this.endPatrol();
              console.log("添加点位失败");
              return;
            }
          })
          .catch(function(error) {
            _this.endPatrol();
            console.log(error);
          });
      },
      //巡检中 开始记录后续点位
      addPolylin(latitude, longitude) {
        let _this = this;
        var latlng = L.latLng(_this.myPostion);
        var num = latlng.distanceTo(L.latLng([latitude, longitude]));
        // _this.myPostion = [latitude, longitude];
        //判断是否移动
        if (num >= 50) {
          _this.myPostion = [latitude, longitude];
          axios
            .post(
              _this.SrcApi + "/gps/addGps",
              qs.stringify({
                lineId: _this.lineId,
                userId: _this.userId,
                statusContent: _this.statusContent,
                longitude: longitude,
                latitude: latitude
              }), {
                headers: {
                  "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
                  // Authorization: authorParams
                }
              }
            )
            .then(function(response) {
              if (response.data.state == 1000) {
                console.log('点位添加成功')
                _this.myMarker.clearLayers();
                console.log('点位清除成功');
                console.log(_this.myPolyline)
                if (_this.myPolyline != null) {
                  _this.map.removeLayer(_this.myPolyline);
                }


                _this.lines.push([latitude, longitude]);
                let myMarkers = []
                for (let i = 0; i < _this.lines.length; i++) {
                  let item = _this.lines[i];
                  let marker = new L.Marker(item);
                  myMarkers.push(marker);
                }
                _this.myMarker = L.layerGroup(myMarkers);
                _this.map.addLayer(_this.myMarker);
                _this.myPolyline = L.polyline(_this.lines, {
                  color: "red"
                }).addTo(_this.map);
                // _this.getLines();
              } else {
                Toast({
                  message: "添加点位失败",
                  position: 'bottom',
                  duration: 1000
                });
                // _this.endPatrol();
                console.log("添加点位失败");
                return;
              }
            })
            .catch(function(error) {
              console.log(error);
            });
        } else {
          // console.log("/* 没动过 */");
          let msg = "移动距离" + num + ",不足定点距离"
          Toast({
            message: msg,
            position: 'bottom',
            duration: 1000
          });
        }
      },
      getPosition() {
        let _this = this;
        _this.endNum++
        if (navigator.geolocation) {
          _this.ifFree = true;
          console.log("/* 地理位置服务可用 */");
          navigator.geolocation.getCurrentPosition(
            function(position) {
              let latitude = position.coords.latitude; //纬度
              let longitude = position.coords.longitude; //经度

              latitude = latitude - 0.0026496046771;
              longitude = longitude - (-0.0058125257492);
              // [latitude-0.0026496046771,longitude-(-0.0058125257492)]

              if (_this.myPostion == null) {
                console.log('第一次进来');
                _this.AddMarker(latitude, longitude)
              } else {
                console.log('可以划线');
                _this.addPolylin(latitude, longitude)
              }

            },
            function() {
              console.log("/* 地理位置服务不可用 */");
            }
          );
        } else {
          console.log("/* 地理位置服务不可用 */");
        }
      },
      startPatrol() {
        MessageBox.confirm('是否确定开始巡检?').then(action => {
          let _this = this;
          _this.userId = _this.getUser().creatUserId;
          axios
            .post(
              this.SrcApi + "/line/addLine",
              qs.stringify({
                userId: _this.userId,
                statusContent: _this.statusContent
              }), {
                headers: {
                  "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
                  // Authorization: authorParams
                }
              }
            )
            .then(function(response) {
              if (response.data.state == 1000) {
                console.log(response.data.data);
                _this.lineId = response.data.data;
                _this.getPosition();
                _this.timer = setInterval(_this.getPosition, 5000);
              } else {
                console.log("获取线路Id失败");
                return;
              }
            })
            .catch(function(error) {
              console.log("获取线路Id失败");
              console.log(error);
            });
        });
      },
      windowMapIni() {
        let _this = this;
        if (_this.myPostion == null) {
          Toast({
            message: '无法获取位置，请开始巡检！',
            position: 'bottom',
            duration: 1000
          });
          return;
        }
        _this.showMap = !_this.showMap;
        _this.windowMap = L.map("windowMap", {
          center: _this.myPostion,
          //center: p,
          zoom: 15,
          // crs: L.CRS.EPSG900913
        });
        L.tileLayer(
          "http://webrd0{s}.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=8&x={x}&y={y}&z={z}", {
            subdomains: ["1", "2", "3", "4"],
            attribution: "高德"
          }
        ).addTo(_this.windowMap);

        _this.windowMap.on("click", function(e) {
          if (_this.myMarker != null) {
            _this.myMarker.clearLayers();
          }
          let myMarkers = [];
          let item = [e.latlng.lat, e.latlng.lng];
          let marker = new L.Marker(item);
          myMarkers.push(marker)
          _this.myMarker = L.layerGroup(myMarkers);
          _this.windowMap.addLayer(_this.myMarker);
          _this.selectPosition = item;
          console.log(_this.selectPosition)
        });
      },
      confirm(type) {
        if (type == 2) {
          this.selectPosition = "";
        }
        this.showMap = false;
        console.log(this.selectPosition);
      }
    }
  };
</script>
<style scoped lang="scss">
  #Free {
    width: 100%;
    height: 100%;
    position: relative;

    .mu-primary-color {
      background: #ef4f4f;
    }

    .mu-form-item-content {
      justify-content: center;
    }

    .windowMapBox {
      width: 100%;
      height: 100%;
      position: relative;

      .windowMap {
        position: absolute;
        bottom: 45px;
        left: 0;
        right: 0;
        top: 0;
      }

      .windowMap-foot {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 45px;

        .windowMap-btn {
          width: 50%;
          float: left;
          height: 45px;
          text-align: center;
          line-height: 45px;
          background: #ef4f4f;
          font-size: 18px;
          color: #fff;
        }
      }
    }

    #MapFree {
      width: 100%;
      position: absolute;
      top: 40px;
      left: 0;
      bottom: 1.2rem;
      right: 0;
      overflow: hidden;
    }

    .footer {
      width: 100%;
      height: 1.2rem;
      position: absolute;
      // left: 0;
      bottom: 0;
      overflow: hidden;
      text-align: center;
      line-height: 1.2rem;
      background: #ef4f4f;
      color: #fff;
      font-size: 25px;
      display: flex;
      justify-content: center;
      align-items: center;
      // margin-right: 16px;
    }

    .left {
      left: 0;
    }

    .right {
      right: 0;
    }

    &.demo-2 {
      background-color: #2196f3;
      color: #fff;
    }
  }
</style>



