<template>
  <div id="Resources">

    <mu-drawer :open.sync="openSelect" :docked="docked" width="100%" :right="position === 'right'">
      <mu-form :model="form" class="mu-demo-form" :label-position="labelPosition" label-width="100">
        <mu-form-item prop="statusContent" label="线路类型内容">
          <mu-select v-model="form.statusContent">
            <mu-option v-for="(item,index) in statusContentList" :key="index" :label="item.name" :value="item.name"></mu-option>
          </mu-select>
        </mu-form-item>
        <mu-form-item prop="userId" label="任务">
          <mu-select v-model="form.userId">
            <mu-option v-for="(item,index) in userList" :key="index" :label="item.name" :value="item.id"></mu-option>
          </mu-select>
        </mu-form-item>
        <mu-form-item>
          <mu-button color="primary" @click="">查询</mu-button>
          <mu-button @click="openSelect=!openSelect">取消</mu-button>
        </mu-form-item>
      </mu-form>
    </mu-drawer>

    <mu-drawer :open.sync="open" :docked="docked" width="100%" :right="position === 'right'">
      <mu-form :model="form" class="mu-demo-form" :label-position="labelPosition" label-width="100">
        <mu-form-item prop="userId" label="任务人">
          <mu-select v-model="form.userId">
            <mu-option v-for="(item,index) in userList" :key="index" :label="item.name" :value="item.id"></mu-option>
          </mu-select>
        </mu-form-item>
        <mu-form-item prop="beginTime" label="开始时间">
          <mu-date-input v-model="form.beginTime" type="dateTime" label-float full-width landscape></mu-date-input>
        </mu-form-item>

        <mu-form-item prop="endTime" label="结束时间">
          <mu-date-input v-model="form.endTime" type="dateTime" label-float full-width landscape></mu-date-input>
        </mu-form-item>
        <mu-form-item>
          <mu-button color="primary" @click="add()">提交</mu-button>
          <mu-button @click="open=!open">取消</mu-button>
        </mu-form-item>
      </mu-form>
    </mu-drawer>


    <mt-header title="线路资源">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <div style="height: 44px;width: 100%;display: flex;">
      <div style="width: 100%;">
        <mt-search cancel-text="取消" placeholder="搜索" v-model="selectValue"></mt-search>
      </div>
      <div @click="select" style="width: 55px;height: 44px;text-align: center;line-height: 44px;background: #d9d9d9;color: #26a2ff;">确定</div>
    </div>

    <div class="content">
      <mu-load-more @refresh="refresh" :refreshing="refreshing" loading-text="正在加载.." :loaded-all="isOver" :loading="loading"
        @load="load">
        <mu-expansion-panel v-for="(item,index) in dataList" :key="index">
          <div slot="header">{{item.name}}</div>
          <div class="contentBox">
            <span>名称:</span>
            {{item.name}}
          </div>
          <div class="contentBox">
            <span>类型:</span>
            {{item.statusContent}}
          </div>
          <!-- <mu-button slot="action" flat @click="addTask(item.id)" color="primary">添加到任务</mu-button> -->
          <mu-button slot="action" flat @click="goMap(item.id)" color="primary">查看</mu-button>
        </mu-expansion-panel>
      </mu-load-more>
    </div>
    <div class="footBtn" @click="addPolyline">
      添加
    </div>
  </div>
</template>
<script>
  import axios from "axios";
  import qs from "qs";
  import {
    Toast
  } from "mint-ui";
  export default {
    name: "Resources",
    data() {
      return {
        msg: "线路资源",
        dataList: null,
        refreshing: false,
        loading: false,
        isOver: false,
        text: "List",
        page: 1,
        open: false,
        openSelect: false,
        docked: false,
        position: 'right',
        labelPosition: 'top',
        userList: '',
        selectValue: '',
        statusContentList: [{
            'name': '线路任务'
          },
          // {'name':'点位任务'}
        ],
        form: {
          statusContent: "线路任务",
          userId: '',
          toDoId: '',
          beginTime: '',
          endTime: ''
        }
      };
    },
    created() {
      this.getData();
      this.getUserList();
    },
    methods: {
      show() {
        this.openSelect = !this.openSelect
      },
      dataFormat(num, type) {
        var dd = new Date(num);
        var y = dd.getFullYear();
        var m = dd.getMonth() + 1;
        var d = dd.getDate();
        var h = dd.getHours();
        var i = dd.getMinutes();
        var s = dd.getSeconds();
        m = m < 10 ? '0' + m : m;
        d = d < 10 ? '0' + d : d;
        h = h < 10 ? '0' + h : h;
        i = i < 10 ? '0' + i : i;
        s = s < 10 ? '0' + s : s;
        if (type) {
          return y + '-' + m + '-' + d + ' ' + h + ':' + i + ':' + s
        } else {
          return y + '-' + m + '-' + d
        }
      },
      add() {
        let _this = this;
        if (_this.form.beginTime != null && _this.form.beginTime != undefined) {
          var beginTime = this.dataFormat(_this.form.beginTime.getTime(), true)
        } else {
          return;
        }

        if (_this.form.endTime != null && _this.form.endTime != undefined) {
          var endTime = this.dataFormat(_this.form.endTime.getTime(), true)
        } else {
          return;
        }
        axios
          .post(
            this.SrcApi + "/tasks/addTasks",
            qs.stringify({
              'statusContent': _this.form.statusContent,
              'userId': _this.form.userId,
              'toDoId': _this.form.toDoId,
              'beginTime': beginTime,
              'endTime': endTime
            }), {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
                // Authorization: authorParams
              }
            }
          )
          .then(function(response) {
            if (response.data.state == 1000) {
              _this.open = !_this.open;
              _this.getData();
              Toast({
                message: "添加成功",
                iconClass: "icon icon-success"
              });
            } else {
              return;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      addTask(data) {
        this.form.toDoId = data;
        this.open = !this.open;
      },
      getUserList() {
        let _this = this;
        axios
          .post(
            _this.SrcApi + "/user/list",
            qs.stringify({
              pageNum: 1
            }))
          .then(function(response) {
            if (response.data.state == 1000) {
              _this.userList = response.data.data.list;
              console.log('获取用户信息成功');
            } else {
              _this.endPatrol();
              console.log("获取用户信息失败");
              return;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      getData(type) {
        let _this = this;
        axios
          .post(
            this.SrcApi + "/systemLine/list",
            qs.stringify({
              pageNum: _this.page
            }))
          .then(function(response) {

            _this.refreshing = false;
            _this.loading = false;
            if (response.data.state == 1000) {
              if (response.data.data.endRow == response.data.data.total) {
                _this.isOver = true;
              }
              if (type == 'load') {
                _this.dataList = _this.dataList.concat(response.data.data.list);
                console.log('新增成功');
              } else {
                _this.dataList = response.data.data.list;
                console.log('获取故障点位成功');
              }
            } else {
              _this.endPatrol();
              console.log("获取故障点位失败");
              return;
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      },
      refresh() {
        this.isOver=false;
        this.refreshing = true;
        this.page=1;
        this.getData();
      },
      //加载更多
      load() {
        this.loading = true;
        this.page=this.page+1;
        this.getData('load');
      },
      goMap(data) {
        this.$router.push({
          name: "MapCont",
          params: {
            lineId: data
          }
        });
      },
      addPolyline() {
        this.$router.push({
          name: "addResour",
        });
      },
      select() {
        let _this = this;
        axios
          .post(
            this.SrcApi + "/systemLine/findByNameLike",
            qs.stringify({
              name: _this.selectValue
            }), {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
              }
            }
          )
          .then(function(response) {
            if (response.data.state == 1000) {
              _this.dataList = response.data.data
            } else {
              return;
            }
          })
          .catch(function(error) {
            console.log(error);
          });

      }
    }
  };
</script>
<style scoped lang="scss">
  #Resources {
    position: relative;
    width: 100%;
    height: 100%;

    .content {
      width: 100%;
      position: absolute;
      top: 84px;
      left: 0;
      right: 0;
      bottom: 45px;
      overflow-y: auto;

      .contentBox {
        span {
          display: inline-block;
          width: 1.8rem;
          text-align: right;
          font-size: 16px;
          font-weight: 600;
        }
      }
    }

    .footBtn {
      width: 100%;
      height: 45px;
      position: absolute;
      bottom: 0;
      left: 0;
      background: #ef4f4f;
      text-align: center;
      font-size: 20px;
      line-height: 45px;
      color: #fff;
    }
  }
</style>



