<template>
  <div class="MapBox">
    <div id="mapLocation"></div>
  </div>
</template>
<script>
// import * as L from "leaflet";
export default {
  name: "ddMap",
  data() {
    return {
      msg: "自由巡检"
    };
  },
  created() {},
  mounted() {
    this.getMe();
  },
  methods: {
    getMe() {
      if (navigator.geolocation) {
        console.log("/* 地理位置服务可用 */");
        navigator.geolocation.getCurrentPosition(this.h5ApiSuccess, this.h5ApiError);
      } else {
        console.log("/* 地理位置服务不可用 */");
        
      }
    },
    h5ApiSuccess:(position) => {
        let latitude = position.coords.latitude; //纬度
        let longitude = position.coords.longitude; //经度
        console.log('你的经度纬度分别为' + longitude + ',' + latitude + '。')
    },
    h5ApiError:() => {
        console.log('/* 地理位置请求失败 */')    
    }
  }
};
</script>
<style scoped lang="scss">
.MapBox {
  width: 100%;
  height: 100%;
  #mapLocation {
    width: 100%;
    height: 100%;
  }
}
</style>


